.Location {
	figure {
		background: lightgreen;
	}

	#map {
		width: 100%;
		height: 400px;
		background: #111;
	}

	.btnSet {
		margin-top: 20px;
		display: flex;
		justify-content: space-between;

		button {
			cursor: pointer;
			height: 30px;
			border: 1px solid #888;
			padding: 5px 20px;
			font: 14px/1 'arial';
			color: #555;
		}

		ul {
			display: flex;

			li {
				cursor: pointer;
				border: 1px solid #888;
				height: 30px;
				padding: 5px 20px;
				font: 14px/1 '맑은 고딕';
				color: #555;
				margin: 0px 5px;
				transition: 0.5s;

				&.on {
					background: #555;
					border: 1px solid #555;
					color: #fff;
				}
			}
		}
	}
}
